<template>
  <div>
    <v-checkbox type="checkbox"
      :label="getValue(element, 'Title')"
      v-model="checked">
    </v-checkbox>
  </div>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { getValue, setValue } from "./store";

export default {
  name: "checkbox-element",
  props: ["element"],
  created() {
    this.$store.commit("addProperties",
    {
      element: this.element,
      properties: [
        new PropertyModel("Title", "pudin"),
        new PropertyModel("Checked", true) 
      ]
    });
    this.setValue('Width', 'auto');
    this.setValue('Height', 'auto');
  },
  computed: {
    checked: {
      get() {
        return this.getValue(this.element, 'Checked');
      },
      set() {
        this.setValue(this.element, 'Checked', !this.getValue(this.element, 'Checked'))
      }
    }
  },
  methods: {
    getValue,
    setValue
  }
};
</script>

<style scoped>

</style>



